<template>

  <div style="margin-top: 15px;">

    <el-tabs type="card">
       <el-tab-pane label="" name="Organization" >
           <menu-org></menu-org>
       </el-tab-pane>

       <el-tab-pane label="">
          <el-form>
                <el-row>
                    <el-col :span="6">
                       <el-form-item label="">
                        <el-input prefix-icon="el-icon-edit"  v-model="ServiceName"  size="mini" style="width: 150px"  placeholder=""></el-input>
                       </el-form-item>
                    </el-col>
                  <el-col :span="6">
                    <el-form-item label=":">
                      <el-select  v-model="property" size="mini"  placeholder="" style="width:150px">
                          <el-option  v-for="item in propertyData" :key="item.property" :label="item.mame" :value="item.property"> </el-option>
                      </el-select>
                    </el-form-item>
                  </el-col>
                  <el-col :span="6">
                      <el-form-item style="float: left">
                         <el-button size="mini"  icon="el-icon-search"  type="primary" @click="onServiceClick"> </el-button>
                     </el-form-item>
                  </el-col>
                  <el-col :span="6">
                    <el-form-item  style="float: right">
                        <el-button type="primary" size="mini" icon="el-icon-plus" @click="showAddEmpView"> </el-button>
                    </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                  <el-table
                    :data="ServiceList"
                    v-loading="tableLoading"
                    border
                    size="mini"
                    style="width: 100%">
                    <el-table-column
                      prop="name"
                      align="center"
                      label="">
                    </el-table-column>
                    <el-table-column
                      prop="id"
                      v-if="false"
                      align="center">
                    </el-table-column>
                    <el-table-column
                      prop="price"
                      align="center"
                      label="">
                    </el-table-column>
                    <el-table-column
                      prop="propertyName"
                      align="center"
                      label="">
                    </el-table-column>
                    <el-table-column
                      prop="salaryIndex"
                      align="center"
                      label="(%)">
                    </el-table-column>
                    <el-table-column
                      prop="settleTypeName"
                      align="center"
                      label="">
                    </el-table-column>
                    <el-table-column
                      align="center"
                      label=""
                      width="150">
                      <template slot-scope="scope">
                        <el-button @click="showEditEmpView(scope.row)" style="padding: 3px 10px 3px 10px;margin: 2px" size="small">
                        </el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                  <div style="display: flex;justify-content: space-between;margin: 2px;float: right">
                    <el-pagination
                      background
                      :page-size="10"
                      :current-page="currentPage"
                      @current-change="ServiceTiaChange"
                      layout="prev, pager, next"
                      :total="totalTaiCount">
                    </el-pagination>
                  </div>
                </el-row>
          </el-form>
          <!---->
         <el-form :model="Servicetype" :rules="rules" style="margin: 0px;padding: 0px;">
           <div style="text-align: left">
             <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="ServicedialogVisible"  width="65%">
               <el-row>
                 <el-col :span="8">
                   <el-form-item label=":" prop="name">
                     <el-input prefix-icon="el-icon-edit" v-model="Servicetype.name"  size="mini" style="width: 150px"
                               placeholder=""></el-input>
                   </el-form-item>
                 </el-col>
                 <el-col :span="8">
                   <el-form-item label=":">
                     <el-select  v-model="Servicetype.property" size="mini"  placeholder="" style="width:150px">
                       <el-option  v-for="item in propertyData" :key="item.property" :label="item.mame" :value="item.property"> </el-option>
                     </el-select>
                   </el-form-item>
                 </el-col>
                 <el-col :span="8">
                   <el-form-item label=" :" prop="price">
                     <el-input prefix-icon="el-icon-edit"  v-model="Servicetype.price"   @keyup.native="number"  size="mini" style="width: 150px"
                               placeholder=""></el-input>
                   </el-form-item>
                 </el-col>
               </el-row>
               <el-row>
                 <el-col :span="8">
                   <el-form-item label=":" prop="salaryIndex">
                     <el-input prefix-icon="el-icon-edit" v-model="Servicetype.salaryIndex"   size="mini" style="width: 150px"  @keyup.native="number"
                               placeholder=""></el-input>%
                   </el-form-item>
                 </el-col>

                 <el-col :span="8">
                   <el-form-item label=":" prop="phone">
                      <el-select  v-model="Servicetype.settleType" size="mini"  placeholder="" style="width:150px">
                       <el-option  v-for="item in settleTypeData" :key="item.settleType" :label="item.mame" :value="item.settleType"> </el-option>
                     </el-select>
                   </el-form-item>
                 </el-col>
               </el-row>
               <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt"> </el-button>
              <el-button size="mini" type="primary" @click="addServiceOnclick"> </el-button>
               </span>
             </el-dialog>
           </div>
         </el-form>
         <!---->
         <el-form :model="Servicetype2" :rules="rules"  style="margin: 0px;padding: 0px;">
           <div style="text-align: left">
             <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="MaydifydialogVisible"  width="65%">
               <el-row>
                 <el-col :span="8">
                   <el-form-item label=":" prop="name">
                     <el-input prefix-icon="el-icon-edit" v-model="Servicetype2.name"  size="mini" style="width: 150px"
                               placeholder=""></el-input>
                   </el-form-item>
                 </el-col>
                 <el-col :span="8">
                   <el-form-item label=":">
                     <el-select  v-model="Servicetype2.property" size="mini"  placeholder="" style="width:150px">
                       <el-option  v-for="item in propertyData" :key="item.property" :label="item.mame" :value="item.property"> </el-option>
                     </el-select>
                   </el-form-item>
                 </el-col>
                 <el-col :span="8">
                   <el-form-item label=" :" prop="price">
                     <el-input prefix-icon="el-icon-edit"  v-model="Servicetype2.price"   @keyup.native="number"  size="mini" style="width: 150px"
                               placeholder=""></el-input>
                   </el-form-item>
                 </el-col>
               </el-row>
               <el-row>
                 <el-col :span="8">
                   <el-form-item label=":" prop="salaryIndex">
                     <el-input prefix-icon="el-icon-edit" v-model="Servicetype2.salaryIndex"  size="mini" style="width: 150px"  @keyup.native="number"
                               placeholder=""></el-input>%
                   </el-form-item>
                 </el-col>

                 <el-col :span="8">
                   <el-form-item label=":" prop="phone">
                     <el-select  v-model="Servicetype2.settleType" size="mini"  placeholder="" style="width:150px">
                       <el-option  v-for="item in settleTypeData" :key="item.settleType" :label="item.mame" :value="item.settleType"> </el-option>
                     </el-select>
                   </el-form-item>
                 </el-col>
               </el-row>
               <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt"> </el-button>
              <el-button size="mini" type="primary" @click="MadfiyServiceOnclick"> </el-button>
               </span>
             </el-dialog>
           </div>
         </el-form>
       </el-tab-pane>
       <el-tab-pane label="">
            <el-form v-model="docfile" :rules="rules" >
               <el-row>
                    <el-col :span="6">
                      <el-form-item label="" prop="CodeName">
                           <el-input prefix-icon="el-icon-edit"  v-model="docfile.CodeName"  size="mini" style="width: 150px"  placeholder=""></el-input>
                      </el-form-item>
                    </el-col>
                 <el-col :span="6">
                   <el-form-item label="" prop="startCode">
                     <el-input prefix-icon="el-icon-edit"  v-model="docfile.startCode"  size="mini" style="width: 150px"  placeholder=""></el-input>
                   </el-form-item>
                 </el-col>
                 <el-col :span="6">
                   <el-form-item label="" prop="endCode">
                     <el-input prefix-icon="el-icon-edit"  v-model="docfile.endCode"  size="mini" style="width: 150px"  placeholder=""></el-input>
                   </el-form-item>
                 </el-col>
                 <el-col :span="6">
                   <el-form-item style="float: left">
                     <el-button size="mini" icon="el-icon-plus"  type="primary" @click="onDocfileClick"></el-button>
                   </el-form-item>
                   <el-form-item style="float: right">
                     <el-button size="mini" icon="el-icon-search"  type="primary" @click="getdocfileList"> </el-button>
                   </el-form-item>
                 </el-col>
               </el-row>
                <el-row>
                  <el-table
                    :data="docfileList"
                    v-loading="tableLoading"
                    border
                    size="mini"
                    style="width: 100%">
                    <el-table-column
                      prop="name"
                      align="center"
                      label="">
                    </el-table-column>
                    <el-table-column
                      prop="id"
                      v-if="false"
                      align="center">
                    </el-table-column>
                    <el-table-column
                      prop="currentCode"
                      align="center"
                      label="">
                    </el-table-column>
                    <el-table-column
                      prop="startCode"
                      align="center"
                      label="">
                    </el-table-column>
                    <el-table-column
                      prop="endCode"
                      align="center"
                      label="">
                    </el-table-column>
                    <el-table-column
                      align="center"
                      label=""
                      width="150">
                      <template slot-scope="scope">
                        <el-button @click="showDocfileView(scope.row)" style="padding: 3px 10px 3px 10px;margin: 2px" size="small">
                        </el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                  <div style="display: flex;justify-content: space-between;margin: 2px;float: right">
                    <el-pagination
                      background
                      :page-size="10"
                      :current-page="currentPage"
                      @current-change="ServiceChange"
                      layout="prev, pager, next"
                      :total="totalCount">
                    </el-pagination>
                  </div>
                </el-row>
            </el-form>
             <el-form :model="docfile2"  style="margin: 0px;padding: 0px;">
           <div style="text-align: left">
             <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="docfileVisible"  width="65%">
               <el-row>
                 <el-col :span="8">
                   <el-form-item label="" >
                     <el-input prefix-icon="el-icon-edit"  v-model="docfile2.name"  size="mini" style="width: 150px"  placeholder=""></el-input>
                   </el-form-item>
                 </el-col>
                 <el-col :span="8">
                   <el-form-item label="" prop="startCode">
                     <el-input prefix-icon="el-icon-edit"  v-model="docfile2.startCode"  size="mini" style="width: 150px"  placeholder=""></el-input>
                   </el-form-item>
                 </el-col>
                 <el-col :span="8">
                   <el-form-item label="" prop="endCode">
                     <el-input prefix-icon="el-icon-edit"  v-model="docfile2.endCode"  size="mini" style="width: 150px"  placeholder=""></el-input>
                   </el-form-item>
                 </el-col>
                 <el-col :span="14">
                   <el-form-item style="float: right">
                     <el-button size="mini" icon="el-icon-edit"  type="primary" @click="madyifdocfile"></el-button>
                   </el-form-item>
                 </el-col>
               </el-row>
             </el-dialog>
           </div>
         </el-form>
       </el-tab-pane>
    </el-tabs>

  </div>


</template>

<script>
  import Organization from './basic/Organizationed'

  export default {
    data() {
      return {
        ServiceName:'',
        property:'',
        dialogTitle:'',
        docfileVisible:false,
        tableLoading:false,
        ServicedialogVisible:false,
        MaydifydialogVisible:false,
        ServiceList:[],
        docfileList:[],
        totalCount:-1,
        totalTaiCount:-1,
        currentPage: 1,
        Servicetype:{
          name:'',
          property:'',
          price:'',
          salaryIndex:'',
          settleType:'',
        },
        Servicetype2:{
          name:'',
          property:'',
          price:'',
          salaryIndex:'',
          settleType:'',
        },
        serviceId:'',
        docfile:{
          CodeName:'',
          startCode:'',
          endCode:'',
          currentCode:'',
        },
        docfile2:{
          name:'',
          startCode:'',
          currentCode:'',
          endCode:'',
          id:'',
        },


        propertyData:[
          {
            property:'1',
            mame:''
          },{
            property:'2',
            mame:''
          },{
            property:'3',
            mame:''
          }
        ],
        settleTypeData:[
          {
            settleType:'1',
            mame:''
          },{
            settleType:'2',
            mame:''
          }
        ],

        rules:{
          name: [{required: true, message: ':', trigger: 'blur'}],
          price: [{required: true, message: ':', trigger: 'blur'}],
          salaryIndex: [{required: true, message: ':', trigger: 'blur'}],
          CodeName: [{required: true, message: ':', trigger: 'blur'}],
          startCode: [{required: true, message: ':', trigger: 'blur'}],
          endCode: [{required: true, message: ':', trigger: 'blur'}],
        },

      };
    },
    mounted: function () {
      this.onServiceClick();
      this.getdocfileList();
    },

    methods: {
      number(){
        this.Servicetype.salaryIndex=this.Servicetype.salaryIndex.replace(/[^\.\d]/g,'');
        this.Servicetype2.salaryIndex=this.Servicetype.salaryIndex.replace(/[^\.\d]/g,'');
        this.Servicetype.price=this.Servicetype.price.replace(/[^\.\d]/g,'');
        this.Servicetype2.price=this.Servicetype.price.replace(/[^\.\d]/g,'');
      },
      //
      onServiceClick(){
        this.tableLoading=true;
        this.postRequestJson('/busi/baseinfo/caretype/list',{
          name:this.ServiceName,
          property:this.property,
          curPage: this.currentPage,
          projectId:this.user.projectId,
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            if(resp.data.code==="200"){
              this.ServiceList =resp.data.msg.result;
              this.totalTaiCount = resp.data.msg.totalSize;
            }else{
              this.getMssage(resp.data.msg);
            }
          }else{
            this.getMssage(resp.status);
          }
        })
      },

      //
      ServiceChange(ServiceChange){
        this.currentPage = ServiceChange;
        this.getdocfileList();
      },
      ServiceTiaChange(ServiceTiaChange){
        this.currentPage = ServiceTiaChange;
        this.onServiceClick();
      },
      //
      showAddEmpView(){
        this.dialogTitle='';
        this.ServicedialogVisible=true;
        this.Servicetype.price='';
        this.Servicetype.salaryIndex='';
        this.Servicetype.settleType='';
        this.Servicetype.name='';
        this.Servicetype.property='';
      },

      //
      addServiceOnclick(){
        this.postRequestJson('/busi/baseinfo/caretype',{
          name:this.Servicetype.name,
          property:this.Servicetype.property,
          price:this.Servicetype.price,
          salaryIndex:this.Servicetype.salaryIndex,
          settleType:this.Servicetype.settleType,
          projectId:this.user.projectId,
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            this.getMssage(resp.data.msg);
          }else{
            this.getMssage(resp.status);
          }
        })
      },
       //
      MadfiyServiceOnclick(){
        this.putRequestJson('/busi/baseinfo/caretype',{
          id:this.serviceId,
          name:this.Servicetype2.name,
          property:this.Servicetype2.property,
          price:this.Servicetype2.price,
          salaryIndex:this.Servicetype2.salaryIndex,
          settleType:this.Servicetype2.settleType,
          projectId:this.user.projectId,
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            this.getMssage(resp.data.msg);
          }else{
            this.getMssage(resp.status);
          }
        })

      },

      //
      showEditEmpView(row){
        this.dialogTitle='';
        this.MaydifydialogVisible=true;
        this.Servicetype2.price=row.price;
        this.Servicetype2.name=row.name;
        this.serviceId=row.id;
        this.Servicetype2.salaryIndex=row.salaryIndex
      },
      //
      cancelEidt(){
        this.ServicedialogVisible=false;
        this.MaydifydialogVisible=false;
      },
      //
      getMssage(data){
        this.$confirm( data, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.ServicedialogVisible=false;
          this.MaydifydialogVisible=false;
          this.docfileVisible=false;
          this.onServiceClick();
          this.getdocfileList();
        }).catch(() => {
        });
      },

      //
      getdocfileList(){
        this.tableLoading = true;
        this.postRequestJson('/busi/baseinfo/docfile/list',{
          name:'',
          curPage: this.currentPage,
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            if(resp.data.code==="200"){
              this.docfileList =resp.data.msg.result;
              this.totalCount = resp.data.msg.totalSize;
            }else{
              this.getMssage(resp.data.msg);
            }
          }else{
            this.getMssage(resp.status);
          }
        })
      },
       //
      onDocfileClick(){
        this.postRequestJson('/busi/baseinfo/docfile',{
          name:this.docfile.CodeName,
          startCode:this.docfile.startCode,
          endCode:this.docfile.endCode,
          projectId:this.user.projectId,
        }).then(resp=> {
          if (resp && resp.status === 200) {
            this.getMssage(resp.data.msg);
          }else{
            this.getMssage(resp.data.msg);
          }
        })
      },
      //
      showDocfileView(row){
        this.dialogTitle='';
        this.docfileVisible=true;
        this.docfile2.id=row.id;
        this.docfile2.name=row.name;
        this.docfile2.startCode=row.startCode;
        this.docfile2.endCode=row.endCode;
      },
      //
      madyifdocfile(){
        this.putRequestJson('/busi/baseinfo/docfile',{
          name:this.docfile2.name,
          startCode:this.docfile2.startCode,
          currentCode:this.docfile2.startCode,
          endCode:this.docfile2.endCode,
          id:this.docfile2.id,
          projectId:this.user.projectId,
        }).then(resp=> {
          if (resp && resp.status === 200) {
            this.getMssage(resp.data.msg);
          }else{
            this.getMssage(resp.data.msg);
          }
        })
      },

    },
    components: {
      'menu-org': Organization,
    },
    computed: {
    user() {
      return this.$store.state.user;
    },
  }


  };
</script>

<style scoped>

</style>



