<template>

  <div style="margin-top: 20px">
    <el-form>
      <el-row>
        <el-table
          :data="resourceList"
          v-loading="tableLoading"
          border
          size="mini"
          style="width: 100%">
          <el-table-column
            prop="faid"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            prop="id"
            v-if="false"
            align="center">
          </el-table-column>
          <el-table-column
            prop="name"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            prop="url"
            align="center"
            label="URL">
          </el-table-column>
          <el-table-column
            prop="type"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            prop="componet"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            prop="icon"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            prop="scope"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            prop="requesmethod"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            prop="iSDisabled"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            prop="isRequireAuth"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            prop="version"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            align="center"
            label="">
            <template slot-scope="scope">
              <el-button @click="showEditEmpView(scope.row)" style="padding: 3px 10px 3px 10px;margin: 2px" size="small">
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
    </el-form>

    <el-form style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="resourceVisible"  width="65%">
          <el-row>
            <el-col :span="6">
              <el-form-item label=":" >
                <el-input prefix-icon="el-icon-edit"   size="mini" style="width: 150px"  placeholder=""></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="URL:" prop="startCode">
                <el-input prefix-icon="el-icon-edit"    size="mini" style="width: 150px"  placeholder="URL"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label=":" prop="endCode">
                <el-input prefix-icon="el-icon-edit"    size="mini" style="width: 150px"  placeholder=""></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label=":" prop="endCode">
                <el-input prefix-icon="el-icon-edit"    size="mini" style="width: 150px"  placeholder=""></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label=" :" >
                <el-input prefix-icon="el-icon-edit"    size="mini" style="width: 155px"  placeholder=""></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label=":" prop="startCode">
                <el-input prefix-icon="el-icon-edit"   size="mini" style="width: 110px"  placeholder=""></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label=":" prop="endCode">
                <el-input prefix-icon="el-icon-edit"   size="mini" style="width: 95px"  placeholder=""></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label=":" prop="endCode">
                <el-select  v-model="disabledid" size="mini"  placeholder="" style="width:135px">
                  <el-option  v-for="item in disabledData" :key="item.disabledid" :label="item.mame" :value="item.disabledid"> </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label=":" >
                <el-select  v-model="requireid" size="mini"  placeholder="" style="width:100px">
                  <el-option  v-for="item in requireData" :key="item.requireid" :label="item.mame" :value="item.requireid"> </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label=":" >
                <el-input prefix-icon="el-icon-edit"    size="mini" style="width: 150px"  placeholder=""></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="13">
              <el-form-item style="float: right">
                <el-button size="mini" icon="el-icon-edit"  type="primary" @click="madyifResource"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-dialog>
      </div>
    </el-form>
  </div>




</template>

<script>
  export default {
    data() {
      return {
        resourceList:[],
        tableLoading:false,
        dialogTitle:'',
        resourceVisible:false,
        disabledid:'1',
        requireid:'1',
        requireData:[
          {
            requireid:'1',
            mame:''
          },{
            requireid:'0',
            mame:''
          }
        ],
        disabledData:[          {
          disabledid:'1',
          mame:''
        },{
          disabledid:'0',
          mame:''
        }
        ],
      };
    },
    mounted: function () {

      this.resourceVisible=true;

    },
    methods: {
      showEditEmpView(row){
        this.resourceVisible=true;
      },
      madyifResource(){
        this.dialogTitle="";
        this.resourceVisible=false;
      },
    }
  }
</script>

<style scoped>

</style>



