        dialogTitle: '',
        tableLoading: false,
        hideRow: false,
        SdialogTitle: '',
        orgdialogTitle: '',
        totalCount: -1,
        currentPage: 1,
        serviceDay: '',
        patientCode: '',
        resourceSelection: [],
