          sex: '',
          address: '',
          phone: '',
          linkman: '',
        },
        transactionRecord: {
          payMan: '',
          money: 0,
          date: '',
        },
        defaultProps: {
          children: 'children',
          label: 'label',
          id: 'id',
          isLeaf: 'isLeaf',
          disabled: 'disabled'
        },
        sexData: [
          {
            sex: '1',
